.rsvp-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.ornaments-wrapper {
		z-index: 3;
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 120px;
			transform: translate(-20%, 0) scaleX(-1);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 80px;
			transform: scale(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100px;
			transform: translate(-40%, 20%);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 100px;
			transform: translate(40%, 20%) scaleX(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 8rem 3rem;
		color: var(--color-light);
		@include max-lg {
			padding: 7rem 2rem;
		}
		.elements-wrapper-absolute {
			.background-element {
				img {
					border-radius: var(--border-radius);
					border: 2px solid $stroke-color;
				}
			}
		}
		.rsvp-form {
			position: relative;
			z-index: 5;
			.title-section {
				&::before {
					all: unset;
				}
			}
		}
	}
}
