.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100px;
		transform: translate(-20%, 0) scaleY(-1);
		@include min-md {
			width: 120px;
		}
		@include min-lg {
			width: 140px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(20%, 0);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-3 {
		position: absolute;
		top: 50%;
		left: 0;
		height: 100%;
		transform: translate(0, -50%);
		.image-element {
			width: auto;
			height: 100%;
			.img-fluid {
				width: auto;
				height: 100%;
				opacity: 0.2;
			}
		}
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				background-color: var(--color-primary);
				color: var(--color-light);
				border-radius: 16px;
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
	}
}
