.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		right: 0;
		height: 95%;
		transform: translate(0, -50%) scale(-1);
		.image-element {
			width: auto;
			height: 100%;
			.img-fluid {
				width: auto;
				height: 100%;
				opacity: 0.2;
			}
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
